import React from "react";
import Img from "../img/img.jpg";

function HomeExample() {
	return (
		<div className="home">
			<MainImage />
			<MainAbout />
		</div>
	);
}

const MainAbout = () => {
	return (
		<div className="about">
			<MainContainer />
		</div>
	);
};

const MainContainer = () => {
	return (
		<div className="container">
			<h2 className="about__title"> </h2>
			{listText}
		</div>
	);
};

const MainImage = () => {
	return (
		<div className="main-screen">
			<img className="main-screen__images" src={Img} />
		</div>
	);
};

const text = [
	`      1712  
	   I.`,

	`  300 ,    
	.        , 
	    .   , 
	,    
	 .    ,
	        
	,     
	.`,

	`       , ,
	,      ,  ,
	 .`,
];
const listText = text.map((text) => <p className="about__text">{text}</p>);

export { HomeExample };